body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #ffffff;
  color: white;
}

.app-container {
  padding: 2rem;
  max-width: 100%;
  overflow-x: auto;
}

h1 {
  color: #000000;
  margin-bottom: 1rem;
}

input[type="file"] {
  padding: 0.5rem;
  background-color: #8b8b9b;
  border: none;
  color: #1b2f2f;
  font-weight: bold;
  cursor: pointer;
  border-radius: 5px;
}

.loading {
  margin-top: 1rem;
  font-size: 1.2rem;
}

.metadata-table {
  margin-top: 2rem;
  width: 100%;
  border-collapse: collapse;
  background-color: rgba(103, 101, 101, 0.13);
  color: #000000;
  border-radius: 6px;
  overflow: hidden;
}

.metadata-table th,
.metadata-table td {
  padding: 0.5rem 0.8rem;
  border: 1px solid #ddd;
  font-size: 0.85rem;
  text-align: left;
}

.metadata-table th {
  background-color: #8b8b9b;
  color: #1b2f2f;
  font-weight: bold;
}

.metadata-table tr:nth-child(even) {
  background-color: #f5f5f5;
}
